<template>
  <div class="image-container set-full-height" style="background-color:#EAF4F4">
    <div class="container">
      <div class="row">
        <div class="col-sm-8 col-sm-offset-2">
          <div class="wizard-container">
            <div class="card wizard-card" data-color="azure" id="wizard">
              <form action="" method="">
                <div class="wizard-header">
                  <h3 class="wizard-title">Become an oracle in three simple steps</h3>
                  <p class="category">Specify the labels and pass the vote</p>
                </div>
                <div class="wizard-navigation">
                  <div class="progress-with-circle">
                    <div class="progress-bar" role="progressbar" aria-valuenow="1" aria-valuemin="1" aria-valuemax="3" :style="progressByPercent"></div>
                  </div>
                  <ul class="nav nav-pills">
                    <li class="active" style="width: 33.3333%;">
                      <a @click="setStep(1)">
                        <div class="icon-circle">
                          <i class="ti-id-badge"></i>
                        </div>
                        Oracle Label
                      </a>
                    </li>
                    <li v-bind:class="{ active: step > 1 }" style="width: 33.3333%;">
                      <a @click="setStep(2)">
                        <div class="icon-circle">
                          <i class="ti-eye"></i>
                        </div>
                        Add Metrics
                      </a>
                    </li>
                    <li v-bind:class="{ active: step > 2 }" style="width: 33.3333%;">
                      <a @click="setStep(3)">
                        <div class="icon-circle">
                          <i class="ti-list"></i>
                        </div>
                        Metric Labels
                      </a>
                    </li>
                  </ul>
                </div>
                <div class="tab-content">
                    <div class="tab-pane" v-bind:class="{ active: step === 1 }" id="details">
                      <div class="row">
                          <div class="col-sm-12">
                              <h5 class="info-text">The unique oracle label will allow users to identify you among other oracles</h5>
                          </div>
                          <div class="col-sm-10 col-sm-offset-1">
                              <div class="form-group">
                                  <label>Enter the oracle label and click "Set Oracle Label"</label>
                                  <input v-model="oracleLabel" type="text" class="form-control" id="exampleInputEmail1" placeholder="e.g Silicon Valley">
                              </div>
                          </div>
                          <div v-if="currentOracleLabel" class="col-sm-10 col-sm-offset-1">
                            <h5 class="info-text">The current label of your oracle is "{{currentOracleLabel}}", you can install a new label. If you are satisfied with the current one, go to the "Voiting" tab</h5>
                          </div>
                      </div>
                    </div>
                    <div class="tab-pane" v-bind:class="{ active: step === 2 }">
                        <div class="row">
                          <div class="col-sm-10 col-sm-offset-1">
                              <h5 class="info-text">Specify the metrics and data types that you will update, then wait for the successful completion of the vote. If the vote was successful, go to the "Metric Labels" tab</h5>
                          </div>
                          <div class="col-sm-5 col-sm-offset-1">
                            <div class="form-group">
                                <label>Name of Metric</label>
                            </div>
                          </div>
                          <div class="col-sm-5">
                            <div class="form-group">
                              <label>Type of Metric</label>
                            </div>
                          </div>
                          <div  v-for="metric, index in metrics" :key="index">
                            <div class="col-sm-5 col-sm-offset-1">
                              <div class="form-group">
                                  <input @change="onChangeName($event, index)" type="text" :value=metrics[index].name class="form-control" id="exampleInputEmail1" placeholder="Metric Name">
                              </div>
                            </div>
                            <div class="col-sm-5">
                              <div class="form-group">
                                <select @change="onChangeType($event, index)" class="form-control">
                                  <option value="1">Uint</option>
                                  <option value="2">Int</option>
                                  <option value="3">FloatUint</option>
                                  <option value="4">FloatInt</option>
                                  <option value="5">Address</option>
                                  <option value="6">Bool</option>
                                  <option value="7">String</option>
                                  <option value="8">Date</option>
                                  <option value="9">Bytes32</option>
                                  <option value="10">KYC</option>
                                </select>
                              </div>
                            </div>
                          </div>
                          <button v-if="metrics.length > 1" @click="handleRemoveMetric()" type="button" class="btn btn-primary btn-wd" style="margin-right: 10px;"><i class="ti-trash"></i> Remove Metric</button>
                          <button @click="handleAddMetric()" type="button" class="btn btn-next btn-fill btn-primary btn-wd"><i class="ti-plus"></i> Add Metric</button>
                          <div class="col-sm-10 col-sm-offset-1">
                            <div class="form-group">
                              <label>Description</label>
                              <textarea v-model='description' class="form-control" placeholder="" rows="2"></textarea>
                            </div>
                          </div>
                          <div class="col-sm-12">
                            <div v-if="currentOracleVotings.length" class="form-group">
                              <h5 class="info-text">Your votes:</h5>
                              <div class="col-sm-5">
                                <div class="form-group">
                                    <label>Name</label>
                                </div>
                              </div>
                              <div class="col-sm-2">
                                <div class="form-group">
                                    <label>Type</label>
                                </div>
                              </div>
                              <div class="col-sm-3">
                                <div class="form-group">
                                    <label>Status</label>
                                </div>
                              </div>
                              <div class="col-sm-2">
                                <div class="form-group">
                                    <label>Votes</label>
                                </div>
                              </div>
                              <div v-for="voting, index in currentOracleVotings" :key="index">
                                <div class="col-sm-5">
                                  <div class="form-group">
                                      <div type="text" class="form-control">{{voting.name}}</div>
                                  </div>
                                </div>
                                <div class="col-sm-2">
                                  <div class="form-group">
                                      <div type="text" class="form-control">{{ getType(voting.metricType) }}</div>
                                  </div>
                                </div>
                                <div class="col-sm-3">
                                  <div class="form-group">
                                      <div type="text" class="form-control">{{ getStatus(voting.endTime) }}</div>
                                  </div>
                                </div>
                                <div class="col-sm-2">
                                  <div class="form-group">
                                      <div type="text" class="form-control">{{voting.support}}</div>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </div>
                      </div>
                    </div>
                    <div class="tab-pane" v-bind:class="{ active: step === 3 }" id="description">
                      <div class="row">
                        <h5 v-if="currentNamesAndLabels.length" class="info-text col-sm-10 col-sm-offset-1">Metrics that you can change and their labels, you can set a label or change it to a new one</h5>
                        <h5 v-else class="info-text col-sm-10 col-sm-offset-1">Wait until the community approves your metrics</h5>
                        <div v-if="currentNamesAndLabels.length">
                          <div class="col-sm-5 col-sm-offset-1">
                            <div class="form-group">
                                <label>Name of Metrics</label>
                            </div>
                          </div>
                          <div class="col-sm-5">
                            <div class="form-group">
                                <label>Name of Labels</label>
                            </div>
                          </div>
                        </div>
                        <div v-for="obj, index in currentNamesAndLabels" :key="index">
                          <div class="col-sm-5 col-sm-offset-1">
                            <div class="form-group">
                                <div type="text" class="form-control">{{obj.name}}</div>
                            </div>
                          </div>
                          <div class="col-sm-5">
                            <div class="form-group">
                                <input @change="onChangeLabelForSet($event, index)" type="text" :value=currentNamesAndLabels[index].label class="form-control" id="exampleInputEmail1" placeholder="Metric Label">
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                </div>
                <div class="wizard-footer">
                  <div class="pull-right">
                    <input v-if="step === 1" @click="handleSetLabel()" type='button' class='btn btn-next btn-fill btn-primary btn-wd' name='next' value='Set Oracle Label' />
                    <input v-if="step === 2" @click="handleBecomeOracle()" type='button' class='btn btn-next btn-fill btn-primary btn-wd' name='next' value='Become Oracle' />
                    <input v-if="step === 3" @click="handleSetMetricLabels()" type='button' class='btn btn-next btn-fill btn-primary btn-wd' name='next' value='Save' />
                  </div>
                  <div class="clearfix"></div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div> 
</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import Oracle from "../components/Oracle.vue";
import {
  getFirstAddress,
  fetchContractData
} from '../utils/web3.js';
import { 
  // methodsByType,
  // converter,
  typeStringByType,
  getHash
} from '@/utils/metricTypes.js'

export default {
  data() {
        return {
          step: 1,
          currentOracleLabel: '',
          currentOracleAddress: '',
          currentOracleVotings: [],
          metrics: [
            {
              name: '',
              type: '1'
            }
          ],
          changedEls: [],
          currentNamesAndLabels: [],
          oracleLabel: '',
          description: '',
        }
    },
  name: 'Main',
  async created() {
    this.currentOracleAddress = await getFirstAddress();
    const oracleinfo = await fetchContractData('getOracleInfo', [this.currentOracleAddress]);
    await this.GET_ORACLE_PROPOSALS();
    const currentOracleVotings = this.ORACLE_PROPOSALS.filter(el => el.oracle.toLowerCase() === this.currentOracleAddress.toLowerCase())
    if(currentOracleVotings.length) {
      for(let i = 0; i < currentOracleVotings.length; i++) {
        const names = currentOracleVotings[i].names;
        for(let j = 0; j < currentOracleVotings[i].names.length; j++) {
          const obj = {
            endTime: currentOracleVotings[i].endTime,
            metricType: currentOracleVotings[i].metricTypes[j],
            name: currentOracleVotings[i].names[j],
            support: currentOracleVotings[i].support
          }
          this.currentOracleVotings.push(obj);
        }
      }
    }
    if(oracleinfo[1]) {
      this.step = 2;
    }
    this.currentOracleLabel = oracleinfo[1];
    if(oracleinfo[0].length) {
      this.step = 3;
      for(let i = 0; i < oracleinfo[0].length; i++) {
        const hash = getHash(oracleinfo[0][i]);
        const metricinfo = await fetchContractData('getMetricaInfo', [hash])
        this.currentNamesAndLabels.push({name: oracleinfo[0][i], label: metricinfo.label})
      }
    }
  },
  components: {
    Oracle
  },
  computed: {
    ...mapGetters(['IS_CONNECTED','LOADER','ORACLE_PROPOSALS']),
    progressByPercent() {
      switch (this.step) {
        case 1:
          return 'width: 21%;'
        case 2:
          return 'width: 50%;'
        case 3:
          return 'width: 79%;'
      }
    },
  },
  methods: {
    ...mapActions(['CONNECT_METAMASK','SEND_DATA','GET_ORACLE_PROPOSALS','SET_LOADER_STATUS']),
    getStatus(timeStamp) {
      const currentTimeStamp = (+new Date()) / 1000;
      if(currentTimeStamp > timeStamp) {
        return 'Finished'
      } else {
        return 'In Progress'
      }
    },
    getType(type) {
      return typeStringByType[type]
    },
    handleAddMetric() {
      this.metrics.push({
              name: '',
              type: '1'
            })
    },
    handleRemoveMetric() {
      this.metrics.pop();
    },
    setStep(step) {
      this.step = step;
    },
    onChangeLabelForSet(event, index) {
      this.currentNamesAndLabels[index].label = event.target.value; 
      this.changedEls.push(index)
    },
    onChangeType(event, index) {
      this.metrics[index].type = event.target.value; 
    },
    onChangeName(event, index) {
      this.metrics[index].name = event.target.value;
    },
    async handleSetLabel() {
      if (!this.oracleLabel) {
        this.$vToastify.info('Enter oracle label', 'Validation warning')
      } else {
        const label = this.oracleLabel
        const obj = {
          method: 'setOracleLabel',
          params: [label]
        }
        await this.SEND_DATA(obj)
        this.SET_LOADER_STATUS(true)
        let pingTimer; 
        pingTimer = setInterval(async () => {
          const actualLabel = await this.getOracleLabel();
          if (actualLabel === label) {
            clearInterval(pingTimer);
            this.currentOracleLabel = label;
            this.step = 2;
            this.SET_LOADER_STATUS(false)
          }
        }, 2000)
      }
    },
    async getOracleLabel() {
      const oracleinfo = await fetchContractData('getOracleInfo', [this.currentOracleAddress]);
      return oracleinfo[1];
    },
    async handleBecomeOracle() {
      if(!this.description) {
        this.$vToastify.info('Fill description', 'Validation warning')
      } else {
        const names = [];
        const types = [];
        this.metrics.forEach(function callback(currentValue, index, array) {
          if(array[index].name.trim()) {
            names.push(currentValue.name.trim());
            types.push(currentValue.type);
          } 
        })
        if(names.length === 0) {
          this.$vToastify.info('Enter at least one name', 'Validation warning')
        } else {
          const obj =  {
            method: 'becomeOracle',
            params: [names, types, this.description]
          }
          await this.GET_ORACLE_PROPOSALS();
          const lengthBefore = this.ORACLE_PROPOSALS.length;
          await this.SEND_DATA(obj);
          this.SET_LOADER_STATUS(true);
          let pingTimer; 
          pingTimer = setInterval(async () => {
            await this.GET_ORACLE_PROPOSALS();
            const actualLength = this.ORACLE_PROPOSALS.length;
            if (actualLength > lengthBefore) {
              clearInterval(pingTimer);
              this.currentOracleVotings = [];
              const currentOracleVotings = this.ORACLE_PROPOSALS.filter(el => el.oracle.toLowerCase() === this.currentOracleAddress.toLowerCase())
              for(let i = 0; i < currentOracleVotings.length; i++) {
                const names = currentOracleVotings[i].names;
                for(let j = 0; j < currentOracleVotings[i].names.length; j++) {
                  const obj = {
                    endTime: currentOracleVotings[i].endTime,
                    metricType: currentOracleVotings[i].metricTypes[j],
                    name: currentOracleVotings[i].names[j],
                    support: currentOracleVotings[i].support
                  }
                  this.currentOracleVotings.push(obj);
                }
              }
              this.metrics = [{
                name: '',
                type: '1'
              }];
              this.SET_LOADER_STATUS(false);
            }
          }, 2000)
        }
      }
    },
    async handleSetMetricLabels() {
      let els = [...new Set(this.changedEls)];
      if(els.length === 0) {
        this.$vToastify.info('Make changes to save them', 'Validation warning')
        return
      }
      const checkupMetric = this.currentNamesAndLabels[els[0]].name;
      const checkupLabel = this.currentNamesAndLabels[els[0]].label;
      const checkupHash = getHash(checkupMetric)
      const names = [];
      const labels = [];
      for(let i = 0; i < els.length; i++) {
        const targetIndex = els[i];
        if(this.currentNamesAndLabels[targetIndex].label) {
          names.push(this.currentNamesAndLabels[targetIndex].name);
          labels.push(this.currentNamesAndLabels[targetIndex].label);
        }
      }
      if(names.length === 0) {
          this.$vToastify.info('Change at least one name and label', 'Validation warning')
      } else {
        const obj =  {
          method: 'setMetricLabels',
          params: [names, labels]
        }
        await this.SEND_DATA(obj)
        this.SET_LOADER_STATUS(true)
        let pingTimer; 
        pingTimer = setInterval(async () => {
          const metricinfo = await fetchContractData('getMetricaInfo', [checkupHash])
          if (metricinfo.label === checkupLabel) {
            clearInterval(pingTimer);
            const oracleinfo = await fetchContractData('getOracleInfo', [this.currentOracleAddress]);
            this.currentNamesAndLabels = []
            for(let i = 0; i < oracleinfo[0].length; i++) {
              const hash = getHash(oracleinfo[0][i]);
              const metricinfo = await fetchContractData('getMetricaInfo', [hash])
              this.currentNamesAndLabels.push({name: oracleinfo[0][i], label: metricinfo.label})
            }
            this.SET_LOADER_STATUS(false)
          }
        }, 2000)
      }
    }
  },
}
</script>
